import S from 'fluent-json-schema'
import {
  Block,
  ClientEntity,
  Page,
  PageSearchQuery,
  iBigInt,
  sBCAddress,
  sBCHash,
  sBigInt,
  sBlock,
  sClientEntity,
  sPageSearchQuery,
  sStringId,
} from '../core'
import { TRANSACTIONS_ROUTES_KEY } from './transaction.const'

/**
 * Client Transaction interface
 */
export interface Transaction extends TransactionData, ClientEntity {}

export function sTransaction(isPartial?: boolean) {
  return S.object()
    .id('#transaction')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sTransactionData(isPartial))
}

export interface TransactionData {
  vaultId: string
  vaultAddress: string
  walletId: string
  walletAddress: string
  tokenId: string
  type: TransactionType
  hash: string
  block: Block
  amount: iBigInt
  tokenAmount: iBigInt
  price: iBigInt
  input?: string
}

export function sTransactionData(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('vaultId', sStringId())
    .description('The vault IDentifier.')
    .prop('vaultAddress', sBCAddress())
    .description('The vault blockchain address.')
    .prop('walletId', sStringId())
    .description('The IDentifier of the wallet.')
    .prop('walletAddress', sBCAddress())
    .description('The wallet blockchain address.')
    .prop('tokenId', sStringId())
    .description('The token IDentifier.')
    .prop('type', sTransactionType())
    .description('The transaction type.')
    .prop('hash', sBCHash())
    .description('The transaction hash.')
    .prop('block', sBlock())
    .description('The transaction blockchain block.')
    .prop('amount', sBigInt())
    .description('The transaction amount.')
    .prop('tokenAmount', sBigInt())
    .description('The transaction token amount.')
    .prop('price', sBigInt())
    .description('The transaction vault price.')
    .prop('input', S.string())
    .description('The transaction input data.')
    .required(
      isPartial
        ? []
        : [
            'vaultId',
            'vaultAddress',
            'walletId',
            'walletAddress',
            'tokenId',
            'type',
            'hash',
            'block',
            'amount',
            'tokenAmount',
            'price',
          ]
    )
}

export type TransactionType =
  | 'DEPOSIT'
  | 'REDEEM'
  | 'HARVEST'
  | 'START_EPOCH'
  | 'STOP_EPOCH'
  | 'GET_INSTANT_WITHDRAWS'
  | 'CLAIM_WITHDRAW'
  | 'CLAIM_INSTANT_WITHDRAW'
  | 'REQUEST_DEPOSIT'
  | 'DELETE_DEPOSIT_REQUEST'
  | 'PROCESS_DEPOSIT_QUEUE'
  | 'CLAIM_DEPOSIT_REQUEST'
  | 'REQUEST_WITHDRAW'
  | 'DELETE_WITHDRAW_REQUEST'
  | 'PROCESS_WITHDRAW_QUEUE'
  | 'CLAIM_WITHDRAW_REQUEST'
  | 'PROCESS_WITHDRAW_CLAIMS'
  | 'DISTRIBUTED_REWARDS'

export function sTransactionType() {
  return S.string().enum([
    'DEPOSIT',
    'REDEEM',
    'HARVEST',
    'START_EPOCH',
    'STOP_EPOCH',
    'GET_INSTANT_WITHDRAWS',
    'CLAIM_WITHDRAW',
    'CLAIM_INSTANT_WITHDRAW',
    'REQUEST_DEPOSIT',
    'DELETE_DEPOSIT_REQUEST',
    'PROCESS_DEPOSIT_QUEUE',
    'CLAIM_DEPOSIT_REQUEST',
    'REQUEST_WITHDRAW',
    'DELETE_WITHDRAW_REQUEST',
    'PROCESS_WITHDRAW_QUEUE',
    'CLAIM_WITHDRAW_REQUEST',
    'PROCESS_WITHDRAW_CLAIMS',
    'DISTRIBUTED_REWARDS',
  ])
}

export type TransactionFields =
  | '_id'
  | 'vaultId'
  | 'vaultAddress'
  | 'walletId'
  | 'walletAddress'
  | 'tokenId'
  | 'type'
  | 'hash'
  | 'block'
  | 'amount'
  | 'tokenAmount'
  | 'price'
  | 'input'
  | 'createdAt'
  | 'createdBy'
  | 'updatedAt'
  | 'updatedBy'

export const TRANSACTION_FIELDS = [
  '_id',
  'vaultId',
  'vaultAddress',
  'walletId',
  'walletAddress',
  'tokenId',
  'type',
  'hash',
  'block',
  'amount',
  'tokenAmount',
  'price',
  'input',
  'createdAt',
  'createdBy',
  'updatedAt',
  'updatedBy',
]

export const TRANSACTION_SORT_FIELDS = ['_id', 'block', 'timestamp']

export type TransactionsSideData =
  | 'wallet'
  | 'user'
  | 'token'
  | 'vault'
  | 'operator'

export interface TransactionsSearchQuery
  extends PageSearchQuery<'block' | 'timestamp', TransactionFields> {
  vaultId?: string | string[]
  vaultAddress?: string | string[]
  walletId?: string | string[]
  walletAddress?: string | string[]
  block?: string
  tokenId?: string
  type?: TransactionType | TransactionType[]
  'timestamp:gte'?: number
  'timestamp:lte'?: number
}

// Route schema
export function sTransactionsSearchQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('vaultId', S.array().minItems(1).maxItems(200).items(sStringId()))
    .description('The vault IDentifier of the transaction.')
    .prop(
      'vaultAddress',
      S.array().minItems(1).maxItems(200).items(sBCAddress())
    )
    .description('The vualt address of the transaction.')
    .prop('walletId', S.array().minItems(1).maxItems(200).items(sStringId()))
    .description('The wallet IDentifier of the transaction.')
    .prop(
      'walletAddress',
      S.array().minItems(1).maxItems(200).items(sBCAddress())
    )
    .description('The wallet address of the transaction.')
    .prop('tokentId', sStringId())
    .description('The token IDentifier of the transaction.')
    .prop('type', S.array().minItems(1).maxItems(200).items(sTransactionType()))
    .description('The type of the transaction.')
    .prop('block', S.string())
    .description('The block of the transaction.')
    .prop('timestamp:gte', S.number())
    .description('Start timestamp of the transaction.')
    .prop('timestamp:lte', S.number())
    .description('End timestamp of the transaction.')
    .extend(sPageSearchQuery(TRANSACTION_FIELDS, TRANSACTION_SORT_FIELDS))
}

export enum TransactionRoutes {
  v1Create = `v1/${TRANSACTIONS_ROUTES_KEY}`,
  v1Delete = `v1/${TRANSACTIONS_ROUTES_KEY}/:tokenId`,
  v1Read = `v1/${TRANSACTIONS_ROUTES_KEY}/:tokenId`,
  v1Update = `v1/${TRANSACTIONS_ROUTES_KEY}/:tokenId`,
  v1Search = `v1/${TRANSACTIONS_ROUTES_KEY}`,
}

export interface TransactionsClientModel {
  search: (params?: TransactionsSearchQuery) => Promise<Page<Transaction>>
  list: (params?: TransactionsSearchQuery) => Promise<Transaction[]>
  findOne: (
    params?: TransactionsSearchQuery
  ) => Promise<Transaction | undefined>
  create: (body: TransactionData) => Promise<Transaction>
}
